.header {
	position: relative;
	z-index: 1;
	width: 100%;
	background-color: #DCECFF;
}

.header__nav {
	width: 100%;
	display: flex;
	transform: translateX(50px);
}

.header__nav-list {
	list-style: none;
	display: flex;	
	margin-left: auto;
}

.header__nav-item {
	padding: 30px 50px;
	text-transform: uppercase;
	font-weight: 600;
	font-size: 30px;
	line-height: 37px;
	color: #000;
	position: relative;
	transition: background-color .4s;

	@media (max-width: 1727px) {
		padding: 30px 25px;
		font-size: 28px;
		line-height: 110%;
    }

	&:hover {
		background-color: #9bc8ff;
	}

	& ul {
		position: absolute;
		top: 80px;
		left: 50px;
		z-index: 15;

		display: flex;
		width: 150%;
		flex-direction: column;
		background-color: #DCECFF;
		border-radius: 5px;

		opacity: 0;
		pointer-events: none;
		transition: opacity .4s;
		overflow: hidden;
		box-shadow: 0px 0px 14px -2px #000000;

		& li {
			display: flex;
			flex-wrap: nowrap;
			padding: 10px;
			transition: background-color .4s;
			border-bottom: 1px solid #4e6f98;

			&:hover {
				background-color: #9bc8ff;
			}
		}
		
		& a {
			font-size: 14px;
			line-height: 20px;
			position: relative;
			padding-bottom: 2px;
		}
	}

	&:first-child ul {
		left: 150px;
	}

	&:last-child ul {
		left: auto;
		right: 50px;
	}

	&:hover ul {
		pointer-events: auto;
		opacity: 1;
	}
	
}

.header__nav-sublist {
	list-style: circle;
}