.main {
	position: relative;
	flex: 1 1 auto;
	text-align: center;
	
	height: 800px;
	background-color: #1B1D20;

	background-image: url("../img/1.jpg");
	background-position: right;
	background-repeat: no-repeat;
	background-size: contain;
	background-size: cover;

	@media (max-width: 1770px) {		
		background-size: contain;
    }

	&--catch {background-image: url("../img/6.jpg");}
	&--fish {background-image: url("../img/2.jpg");}
	&--gear {background-image: url("../img/3.jpg");}
	&--fishermen {background-image: url("../img/4.jpg");}
	&--reservoirs {background-image: url("../img/5.jpg");}
}

.main__wrapper {
	width: 100%;
}

.main__content {
	position: relative;
	z-index: 10;
	display: flex;
	width: 500px;
	padding-left: 50px;
	flex-direction: column;
	color: #fff;	
	text-align: left;
	padding-top: 50px;

	&--index {		
		padding-top: 190px;
	}
}

.main__caption {
	font-size: 30px;
	line-height: 37px;
	margin-top: 0;
	font-weight: 600;
	margin-bottom: 30px;	
	text-transform: uppercase;

	&--index {
		font-size: 40px;
		line-height: 49px;		
		margin-bottom: 80px;
	}
}

.main__link {
	font-weight: 700;
	font-size: 20px;
	line-height: 24px;
	margin-top: 15px;
	transition: opacity .4s;

	&:hover {
		opacity: .7;
	}
}

.main__list {
	margin-left: 20px;
}

.main__item {
	font-weight: 700;
	font-size: 20px;
	line-height: 24px;
}

.main__item:not(:last-child) {
	margin-bottom: 15px;
}

.main__sub-caption {
	margin-top: 0;
	font-weight: 500;
	font-size: 20px;
	line-height: 24px;

	&--index {
		font-size: 30px;
		line-height: 37px;
	}
}

.main__button-block {
	display: flex;
	flex-direction: column;
	gap: 30px;
}

.main__button {
	padding: 25px 30px;
	background-color: #fff;
	border-radius: 37.5px;
	border: 2px solid transparent;
	font-weight: 700;
	font-size: 20px;
	line-height: 24px;
	text-align: center;
	color: #000;
	transition: all .4s;

	&:hover {
		background-color: transparent;
		color: #fff;
		border: 2px solid #fff;
	}

	&--secondary {
		width: 75%;
		background-color: transparent;
		color: #fff;
		border: 2px solid #fff;

		&:hover {
			background-color: #fff;
			color: #000;
			border: 2px solid transparent;
		}	
	}
}

.main__text {
	margin-top: 30px;
	margin-bottom: 0;

	&--second {
		margin-top: 160px;
	}

	&--large {
		margin-top: 180px;
		font-weight: 300;
		font-size: 25px;
		line-height: 30px;
		font-weight: 300;
	}

	&--gear {
		margin-top: 50px;
	}
}

.main__overlay {
	position: fixed;
	z-index: 2;
	top: 0;
	left: 0;
	width: 800px;
	height: 100%;
	background-color: #1B1D20;
	clip-path: polygon(0 0, 70% 0, 100% 100%, 0% 100%);

	overflow-y: scroll;
	padding-bottom: 100px;

	&::-webkit-scrollbar {
        width: 0px;
        height: 0px;
        border-radius: 0px;
    }

    &::-webkit-scrollbar-track {
        background: transparent;
    }

    &::-webkit-scrollbar-thumb {
        background-color: transparent;
        border-radius: 0px;
    }
}