.footer {
	background-color: #DCECFF;	
	width: 100%;
}

.footer__wrapper {	
	display: flex;
	width: 100%;	
	max-width: 1728px;
	padding: 30px 50px;
    margin: 0 auto;
}

.footer__content {
	margin-left: auto;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
}

.footer__caption {
	margin-top: 0;
	margin-bottom: 60px;
	display: inline-block;
	text-align: right;
	max-width: 500px;
	font-weight: 300;
	font-size: 30px;
	line-height: 37px;
	text-transform: uppercase;
	
	&--small {
		font-size: 25px;
		line-height: 30px;
		margin-bottom: 15px;
	}
}

.footer__list {
	@include list-style();
	margin-bottom: 50px;	
	text-align: right;
	display: flex;
	flex-direction: column;
	gap: 15px;
}

.footer__link {
	font-weight: 700;
	font-size: 20px;
	line-height: 24px;
	transition: color .4s;
	
	&:hover {
		color: #01326d;
	}
}

.footer__main-link {
	font-weight: 700;
	font-size: 20px;
	line-height: 24px;
	text-transform: uppercase;
	transition: color .4s;
	
	&:hover {
		color: #01326d;
	}
}